.element {
  height: 140px;
  width: 140px;
  position: absolute;
  left: 0;
  top: 0;
  margin: 0 auto;
  border-radius: 50%;
}

.element:before,
.element:after {
  content: '';
  display: block;
  position: absolute;
  border: 50%;
  border: 1px solid #4DCC00;
  left: -0px;
  right: -0px;
  top: -0px;
  bottom: -0px;
  border-radius: 50%;
  opacity: 0;
  backface-visibility: hidden;
}


.pulse .element:before,
.pulse .element:after {
  animation: pulse 1.5s linear infinite;
}

.pulse .element:after {
  animation-duration: 0.5s;
}

@keyframes pulse {
  0% {
    transform: scale(1.6);
    opacity: 0;
  }

  50% {
    opacity: 1;
  }

  100% {
    transform: scale(1.2);
    opacity: 0;
  }
}

.rotate {
  animation: rotate 4s linear infinite;
}

@keyframes rotate {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(359deg);
  }
}
